$bg:rgb(16,16,16);
.banner{
    height: 600px;
    background-color: $bg;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0%;
        left: 0%;
        background: $bg;
        opacity: 0.4;
      }
      h1,
      p {
        color: white;
        font-size: 3rem;
        width: 40%;
        z-index: 10;
      }
      p {
        font-size: 1.2rem;
        letter-spacing: 2px;
        word-spacing: 3px;
        font-weight: 100;
      }
    
      > div {
        display: flex;
    
        button {
          padding: 1rem;
          width: 150px;
          outline: none;
          border: none;
          z-index: 10;
          border-radius: 5px;
          margin: 1rem;
          cursor: pointer;
          font-size: 1.2rem;
          display: flex;
          align-items: center;
          justify-content: center;
          transition: all 0.5s;
          &:hover {
            opacity: 0.6;
          }
    
          &:nth-child(odd) {
            background-color: white;
            color: black;
          }
          &:nth-child(even) {
            background-color: rgb(57, 57, 57);
            color: rgb(255, 255, 255);
          }
          svg {
            font-size: 1.7rem;
            margin: 0 10px;
            color: black;
          }
        }
      }
    }
.row{
    padding: 1rem;
    background-color: black;
    >h2{
     font-weight: 400;  
     margin: 0.3rem; 
    }
    >div{
        display:flex;
        overflow-x: auto;
        
    }
}
.card{
width:200px;
margin: 0.3rem;;
cursor: pointer;
transition: transform 0.5s;
&:hover{
    transform: translateY(-10px);
}
}
@media (max-width:900px) {
    .banner > div button{
        padding: 0.5rem;
    }
    .banner{
        height: 400px;
    }
    .banner h1{
        font-size: 2rem;
    }
    .banner p{
        display: none;
    }
}